// 
export const HOME_LOAD = 'HOME_LOAD'
export default {
    state: {
        homeData: {},
    },
    getters: {
        
    },
    mutations: {
        [HOME_LOAD](state, data) {
            if (data) {
                state.homeData = data
            }
        },
    },
    actions: {
        [HOME_LOAD]({ commit }, data) {
            commit(HOME_LOAD, data)
        }
    }
}