// 
export const COMMON_DATA = 'COMMON_DATA'
export default {
    state: {
        commonData: {},
    },
    getters: {
        
    },
    mutations: {
        [COMMON_DATA](state, data) {
            if (data) {
                state.commonData = data
            }
        },
    },
    actions: {
        [COMMON_DATA]({ commit }, data) {
            commit(COMMON_DATA, data)
        }
    }
}