import Vue from 'vue'
import Router from 'vue-router'

// vue
//
import Home from '../pages/home';
import User from '../pages/user';
import Help from '../pages/help';
import Rule from '../pages/rule';
//
import Wallet from '../pages/wallet';
import Invite from '../pages/invite';
import News from '../pages/news';
import Task from '../pages/task';
import My from '../pages/my';
import Active from '../pages/active';
// 
//
import Pay from '../pages/wallet/pay';
import Recharge from '../pages/wallet/recharge';
import OrderRecords from '../pages/wallet/orderRecords';
import Exchange from '../pages/wallet/exchange';
import DealRecords from '../pages/wallet/dealRecords';
//
import MyInfo from '../pages/my/myInfo';
import MySetting from '../pages/my/mySetting';
import Personal from '../pages/my/personal';
//
import SystemMsg from '../pages/news/systemMsg';
import GameMsg from '../pages/news/gameMsg';
import NewsMsg from '../pages/news/newsMsg';
//
import Course from '../pages/invite/course';
import MyInvite from '../pages/invite/myInvite';
import MyReward from '../pages/invite/myReward';


Vue.use(Router) // vueRouter

// 
const VueRouterPush = Router.prototype.push;
Router.prototype.push = function push(to) {
    return VueRouterPush.call(this, to).catch(err => err);
}

let files = require.context('./../pages').keys();
let routess = [];

files.map(item => {
    item = item.replace('./', '');
    if (item.indexOf(".vue") != -1) {
        item = item.replace(".vue", '');
        let items = item.split("/");
        items.map(children => {
            let route = new Object();
            route.path = '/' + children;
            route.name =  children.charAt(0).toUpperCase() + children.slice(1);
            route.component = () => import('./../pages/' + item + '.vue');
            routess.push(route);
        })
    }
});

let rootRouter = []; //
let allRouter = []; // > 
// 
files.map(item => {
    item = item.replace('./', '');
    let root = item.split('/');
    if (root.length <= 1 && item.indexOf('.vue') == -1) {
        let URI = root[0];
        let path = '/' + URI;
        let name = '/' + URI;
        let component = () => import('./../pages/' + URI + '.vue');
        let children = [];
        rootRouter.push({
            path, name, component, children
        });
    }
    if (item.indexOf('.vue') == -1) {
        allRouter.push(item);
    }
});

// 
function getRootRouter(rootRouter, allRouter) {
    for(let i = 0; i < allRouter.length; i++) {
        let path = rootRouter.path.split("/");
        let all = allRouter[i].split("/");
        for(let s = 0; s < all.length; s++) {
            if (all[s] == path[path.length - 1] && all[s + 1]) {
                let uri = allRouter[i];
                allRouter.splice(i, 1);
                rootRouter.children.push({
                    path: '/' + uri, 
                    name: '/' + uri,
                    component: () => import('./../pages/' + uri + '.vue'),
                    children: []
                });
                // 
                getRootRouter(rootRouter.children[rootRouter.children.length - 1], allRouter);
                break;
            }
        }
    }
    return rootRouter;
}

rootRouter.map(item => {
    return getRootRouter(item, allRouter);
});


// 
const routes = [{
            path: '/',
            name: 'Home',
            redirect: '/home' //
        },
        {
            path: '/home',
            name: 'Home',
            component: Home
        },
        {
            path: '/user', //
            name: 'User',
            component: User,
            redirect: '/user/my',
            children: [{
                    path: '/user/wallet', //
                    name: 'Wallet',
                    component: Wallet,
                    redirect: '/user/wallet/recharge',
                    children: [{
                            path: '/user/wallet/pay', //vip
                            name: 'Pay',
                            component: Pay,
                        },
                        {
                            path: '/user/wallet/dealRecords', //
                            name: 'DealRecords',
                            component: DealRecords,
                        },
                        {
                            path: '/user/wallet/exchange', //
                            name: 'Exchange',
                            component: Exchange,
                        },
                        {
                            path: '/user/wallet/orderRecords', //
                            name: 'OrderRecords',
                            component: OrderRecords,
                        },
                        {
                            path: '/user/wallet/recharge', //
                            name: 'Recharge',
                            component: Recharge,
                        }
                    ]
                },
                {
                    path: '/user/task', //
                    name: 'Task',
                    component: Task,
                },
                {
                    path: '/user/active', //
                    name: 'Active',
                    component: Active,
                },
                {
                    path: '/user/my', //
                    name: 'My',
                    component: My,
                    redirect: '/user/my/myInfo',
                    children: [{
                            path: '/user/my/myInfo', //
                            name: 'MyInfo',
                            component: MyInfo,
                        },
                        {
                            path: '/user/my/mySetting', //
                            name: 'MySetting',
                            component: MySetting,
                        },
                        {
                            path: '/user/my/personal', //
                            name: 'Personal',
                            component: Personal,
                        }
                    ]
                },
                {
                    path: '/user/news', //
                    name: 'News',
                    component: News,
                    redirect: '/user/news/systemMsg',
                    children: [{
                            path: '/user/news/systemMsg', //
                            name: 'SystemMsg',
                            component: SystemMsg,
                        },
                        {
                            path: '/user/news/gameMsg', //
                            name: 'GameMsg',
                            component: GameMsg,
                        },
                        {
                            path: '/user/news/newsMsg', //
                            name: 'NewsMsg',
                            component: NewsMsg,
                        }
                    ]
                },
                {
                    path: '/user/invite', //
                    name: 'Invite',
                    component: Invite,
                    redirect: '/user/invite/myInvite',
                    children: [{
                            path: '/user/invite/myInvite', //
                            name: 'MyInvite',
                            component: MyInvite,
                        },
                        {
                            path: '/user/invite/myReward', //
                            name: 'MyReward',
                            component: MyReward,
                        },
                        {
                            path: '/user/invite/course', //
                            name: 'Course',
                            component: Course,
                        }
                    ]
                },
            ]
        },
        {
            path: '/rule', //
            name: 'Rule',
            component: Rule
        },
        {
            path: '/help', //
            name: 'Help',
            component: Help
        },
    ]
    // Router
var router = new Router({
    routes: rootRouter
})
console.log(routes);
console.log(rootRouter);
// 
export default router;