import Vue from 'vue';

import { Button, Dropdown, DropdownMenu, DropdownItem, Tabs, TabPane, Carousel, CarouselItem, Radio, DatePicker, Collapse } from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App.vue';
import router from './router';
import utils from './utils/common.js';
import config from './config.js';
import store from './store/index';

Vue.use(Button);
Vue.use(Tabs);
Vue.use(TabPane)
Vue.use(Dropdown);
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(Carousel);
Vue.use(CarouselItem);
Vue.use(Radio)
Vue.use(DatePicker)
Vue.use(Collapse)

Vue.config.productionTip = false;
Vue.prototype.utils = utils;
Vue.prototype.utils = config;

new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App)
});