<template>
    <div class="page-home">
        <el-button @click="load()">load</el-button>
        homeData:
        {{homeData}}
        <br />
        commonData
        {{commonData}}
        
        <br />
        <router-link :to="{path : '/home/home2'}">home2</router-link>
        <router-link :to="{path : '/home/home2/home3'}">home3</router-link>
        <router-view />
    </div>
</template>

<script>
export default {
    computed: {
        homeData() {
            return this.$store.state.home.homeData;
        },
        commonData() {
            return this.$store.state.common.commonData;
        }
    },
    mounted() {
        this.$store.commit("HOME_LOAD", {
                home: 'data'
            });
    },
    methods: {
        load () {
            this.$store.commit("HOME_LOAD", {
                home: 'data'
            });
            this.$store.commit("COMMON_DATA", {
                home: 'common'
            });
        }
    }
}
</script>
<style lang="less" scoped>
    @import url('./../assets/style.less');
    .page-home {
        
    }

</style>
